'use client';

import { SessionProvider } from 'next-auth/react';
import { Toaster } from '@axiom-ui/react/toaster';
import { RUMProvider } from './rum-provider';

interface NextAuthProviderProps {
  children?: React.ReactNode;
}

export function Providers({ children }: NextAuthProviderProps) {
  return (
    <SessionProvider>
      <RUMProvider>
        <Toaster />
        {children}
      </RUMProvider>
    </SessionProvider>
  );
}
