import { apm } from '@elastic/apm-rum';

const shouldMonitor = process.env.NEXT_PUBLIC_ELASTIC_RUM_ACTIVE === 'true';

export const initMonitoring = () => {
  if (shouldMonitor) {
    return apm.init({
      serviceName: process.env.NEXT_PUBLIC_ELASTIC_RUM_SERVICE_NAME,
      serverUrl: process.env.NEXT_PUBLIC_ELASTIC_RUM_SERVER_URL,
      serviceVersion: process.env.NEXT_PUBLIC_ELASTIC_RUM_SERVICE_VERSION,
      environment: process.env.NEXT_PUBLIC_ELASTIC_RUM_ENVIRONMENT,
    });
  }
};
